<script>
export default {
      name: "main-text",
      props: ['subjectsName', 'subNumber', 'innerPart', 'nameTitle'],
  data() {
    return {
        arrayOne: [[''],
         ['<p>שעון פעילות הוא גרף המגדיר את העיתוי</br> של מופעי ניהול ההדרכה המרכזיים בהכשרה במסגרת ניהול ההדרכה ביחידת ההכשרה, מתקיימים מופעי הדרכה שתכליתם ניהול שגרת ההכשרות בצורה שיטתית ואיכותית..</p>', ''], 
         ['' , '<p>הגדרות זמנים: <br> אחת לשנה יתבצע ביחידה איתור צרכים לכלל מסגרות היחידה. <br>  תוכנית האבחון תבוצע ברבעון ד’ של שנת העבודה הנוכחית, לקראת גיבוש ת”ע שנתית יחידתית. <br>  מסגרת הזמנים לביצוע האבחון היא רבעון לכל היותר מתחילת התהליך ועד הצגת התוצרים.</p>', '<p>ניהול תהליך איתור צרכים: <br> תהליך האבחון ינוהל ע”י גוף פיתוח הדרכה ביחידה. טרם תחילת התהליך תיכתב פק”א המגדירה את שלבי התהליך, השותפים, לוחות הזמנים, השיטות והמקורות. <br>  פק”א זו תאושר ע”י מפקד בית הספר.</p>',  '<p> איסוף הנתונים: <br>  תהליך האבחון כולל בחינת תהליכי ההדרכה ותוצרי הדרכה ע”פ 4 ממדי ההדרכה. <br> את התהליך יש לבצע ע”פ מחוון קריטריונים למיפוי יחידת ההדרכה.  <br> במסגרת איסוף הנתונים יידגמו המסגרות ביחס של לפחות 1:2 בכל אחת מהמסגרות המכשירות ביחידה לשלב כלים ושיטות מגוונות לאיסוף נתונים.</p> ', '<p>המקורות לאיסוף הנתונים יהיו מגוונים עד כמה שינתן.<br> לאחר תהליך איסוף הנתונים יש לבצע תהליך עיבוד אישי, סטטיסטי וקבוצתי</p>'], 
         ['<p>תוכנית העבודה השנתית של יחידת ההדרכה מגובשת לאור הנחיות המפקד היחידה ומושפעת ממשתנים רבים ובניהם אבחון הדרכתי ליחידה  (ראה נוהל 1.3 ) תוכנית עבודה יחידתית מאפשרת ליחידה להתמקד ולקדם מושאים לצד התפעול השוטף של היחידה. בתוכנית העבודה מביא המפקד לידי ביטוי את חזונו, מטרותיו וסדרי העדיפויות שלו.<br> תוכנית העבודה ההדרכתית היא נגזרת מתוכנית העבודה השנתית של היחידה.</p>', '<p>ניהול תוכנית העבודה: <br> לכל בה"ד תכתב תוכנית עבודה שנתית או דו-שנתית המאושרת על-ידי מפקד היחידה, לכל בה"ד תכתב תוכנית עבודה הדרכתית שנתית או דו-שנתית, הנגזרת, בין היתר, מתוכנית העבודה היחידתית, מתוכנית העבודה של המפקדה הממונה ומתהליך מיפוי צרכים.<br></p> ', '<p>תוכנית העבודה ההדרכתית תכתב ע"י מפקדת הגפ"ה ותאושר אצל מפקד הבה"ד עד ל30.1 בכל שנה תוכנית העבודה ההדרכתית תגלם את כלל הפעולות לשיפור ההכשרות בהובלת גפ"ה היחידה</p>', '', '<p>תוכנית העבודה תכלול את העמודות הבאות: הממד, המשימה, אבני הדרך, שותפים ועיתוי ותאריך גמר ביצוע. <br> כלל דיוני הסטאטוס והנחיות מפקד מאישור תוכנית העבודה וכן מדיוני הסטאטוס יתועדו וישמרו לכל הפחות שנתיים.<br> תוכניות העבודה ההדרכתיות ישמרו לכל הפחות 4 שנים.<br></p>',
            'תוכניות העבודה ההדרכתיות וכן סיכומי דיוני הסטאטוס ישמרו ויונגשו לכלל סגלי פיתוח ההדרכה ואוכלוסיית המג"דים בבה"ד באחריות מפקד גוף פיתוח ההדרכה ביחידה.'], 
         ['<p>ביצוע תהליך סדור של אישורי תוכניות למחזור, וסיכום מחזור הכשרה ע”י מפקד ביה”ס.<br> זהו תהליך המסכם את מחזור ההכשרה בכלל ההיביטים- הדרכתיים, לוגיסטיים וכו’..</p>', '', '<p>שלושת הדיונים המרכזיים בנושא זה אשר ייערכו בראשות המפקד:<br> סיכום מחזור הכשרה, אישורי תוכניות להכנת הסגל ו”כברת דרך”, אישור תוכניות למחזור וכשירות הסגל.</p>',
            '<p>סיכום מחזור ההכשרה כולל שלושה דגשים עיקריים: <br> מידת העמידה במטרות ההכשרה, חוזקות וחולשות בכשירות המבצעית של בוגרי ההכשרה,<br> פערי הסמכות- כשירויות שלא הוגשו עבור כל החניכים או חלקם.<br> קורסי הקצונה- יבוצע ויוצג תחקיר אשר נושאו יאושר ע”י מ’ ביה”ס ויוצג למפקד זרוע היבשה במופע סיום קק”ץ בבה”ד 1.</p>',
            '<p> יתר הקורסים- יש להציג תחקיר בנושא שיאושר ע”י הרע”ן או המג”ד <br> דגשים ותיעוד ניהול ידע- כלל סיכומי ההכשרה-<br>  מצגות, דו”חות וסיכומי הדיונים, יתועדו ויונגשו לכלל המפקדים מדרג המ”פ. <br> המידע יישמר לשנתיים לפחות. <br>בקרה על התהליך, התיעוד וההנגשה, באחריות הגדודים באתר בהל”ג.</p>'], 
         ['<p>סיכומים עיתיים במהלך הקורס הם מנגנונים שמאפשרים מעקב ובקרה אחר מימוש תוכנית ההכשרה,  וכן אחר התקדמות החניכים בתהליך הלמידה, לצורך קבלת החלטות בזמן רלוונונטי בסוגיות שונות.<br>סיכומים עיתיים מהווים גם תחקירי אמצע הממוקדים לפרקי ההכשרה.</p>', '<p>ישנם חמישה סוגים של סיכומים עיתיים:<br> סיכום יומי, סיכום שבועי, סיכומים לשבועות עצימים, סיכום אמצע מחזור, סטאטוס הסמכות.<br>  תיעוד וניהול הידע, הסיכומים ישמרו ויונגשו לפרק זמן של שנתיים.<br> אחריות התיעוד וניהול הידע הוא באחריות קה"ד הגדוד ובבקרה של מפקד הגפ"ה.</p>'], 
         ['', '<p>מטרת הערכת תוכנית ההכשרה (אפקטיביות ההדרכה) היא קבלת החלטות לגבי שינויים ושיפורים בתוכנית ההכשרה ובתהליכי ההדרכה. הערכה בודקת היא הכשרה ממוקדת אחת, במטרה לאתר פערים לשיפור- בתוכנית ההכשרה, בסגל המפקדים ובסביבת ההדרכה.</p>', '<p>במידה ועולה פער בהשגת כשירות מסויימת יש להעמיק בתחקור בנושא, על מנת לגבש המצעות לייעול ושיפור ההכשרה. <br>הנתונים שנאספו ייבדקו לכדי חוזקות, חולשות, מגמות מרכזיות וכאמור בחתך כשירויות.</p>'], 
         ['', '', '<p>עקרונות לביצוע: ביקורות בבה”ד יבוצעו לפחות אחת לתקה”ד (בכל תחום). <br> אחריות ביצוע הבקרות הן של קציני המטה של ביה”ס, כל אחד בתחומו. <br> הביקורות יבוצעו ע”פ פורמט בקרה מטעם מה”י או הזרוע.<br>על פורמט זה ניתן להוסיף נושאים נוספים לשיקול מפקד ביה”ס.</p>', '<p> כל בקרה תכלול את המרכיבים הבאים בכל מסגרת:<br> עיון בחומרים כתובים ומערכות ממוחשבות, שיחה עם מ”פ, שיחת חתך סגל, שיחת חתך חניכים, ממצאי הביקורות יוצגו למפקד המסגרת הנבדקת ולמפקד ביה”ס בדיון ייעודי לנושא.<br> כלל הפורמטים לביקורת הדרכה יונגשו באתר בהל”ג באחריות מוביל הביקורת בבה”ד.</p>'],
         ['', ''],
         ['הקמפוס הדיגיטלי היא מערכת לניהול ההדרכה והלמידה, תומכת בתהליכי למידה בהכשרות, באימונים ומשמשת לניהול כשירות הפרט.<br> עקרונות העבודה: <br> תהליכי ההדרכה הבאים ינוהלו בקמפוס הדיגיטלי: ניהול קורסי אב תואמים לתיקי היסוד, ניהול מחזורי ההכשרה ע"פ קורס האב, תיק חניך - (תיעוד מלא של נתוני הלומד בהכשרה לרבות מבחנים, משובים ודפי הערכה. ), <br> ', 'תיק לאיש סגל - (תיעוד מלא של נתוני איש הסגל לרבות מבחני כשירות, משובים ודפי הערכה.)<br> ועדות הסמכה בהכשרה, תיעוד סטטוס, <br> סיום קורס הקורס של החניך וציון סוף הקורס, ביצוע העברת מקל וקליטת חייל מההכשרה בשטח ע"ב נתוני המערכת,<br> ניהול כשירות הסגל, "כשיר להכשיר" ו"כברת הדרך" ע"ג הקמפוס הדיגיטלי.','בע"ת להטמעת הקמפוס הדיגיטלי:<br> מוביל ההטמעה בבית הספר הוא מפקד הגפ"ה ביחידה. <br> בכל תקה"ד כלל הסגל יעבור השתלמות בבהל"ג ע"י נאמן המערכת על מנת להבטיח יעול מקסימלי של המערכת ושימוש שוטף בזמן אמת', 'בקרה פנימית על הטמעות הקמפוס הדיגיטלי - באחריות נאמן הקמפוס  הדיגיטלי בגדוד או בענף לבצע בקרה שוטפת לאורך המחזור אודות ביצוע ההזנות הנדרשות ע"י הפלוגות תחתיו. באחריות מפקד הגפ"ה ביחידה לבצע אחת למחזור בקרה על הטמעת הקמפוס הדיגיטלי ביחידות.', 'ניהול המערכת - <br> מעקב אחר משימות המשה"ד במהלך המחזור יתבצעו ע"י מנהלי המערכת באמצעות הפקת דוחות, ביקורות ופגישות באופן שבועי בנוסף על פיקוח של מפקד הפלוגה.<br> טכנולוגיה במרחב הלמידה - בעת ביצוע מבחן בקמפוס יתבצע פיקוח מוגבר של אנשי הסגל לשם שמירת מהמינות המבחן.' ]],

        arrayTwo: [['<p>פיתוח הכשרה חדשה מתבצע בדר”כ בעקבות שינויים בתו”ל, תפיסות ואמל”ח, אשר דורשים בעלי תפקידים חדשים שלא היו קיימים בעבר. <br> תהליך פיתוח הכשרה חדשה:<br> עיבוד ניתוח התפקיד, קביעת רציונל ההכשרה, ניתוח הכשירויות והמדדים לידע ומיומנויות נדרשות.</p>', '<p> הגדרת מצבי המבחן המסכם, הגדרת אופן הערכת המבחן, בניית מערכת שעות עקרונית להכשרה, הגדרת התשתיות והמשאבים הנדרשים להכשרה, הגדרת לו”זים ותחומי אחריות, מפקד צוות הפיתוח יגדיר את השלבים והלו”ז בתהליך הפיתוח</p>'], 
        ['<p>תיק היסוד הינו המסמך מחייב לכל קורס או השתלמות ומתועדים בו מבנה הקורס והתנאים הנדרשים לקיומו, כתוצר של תהליך פיתוח הדרכתי.<br>  כתיבת תיק יסוד: <br> יחידה המבצעת הכשרה, תכתוב את תיק היסוד בשיתוף עם הגורם החילי הרלוונטי,  יש לשלב בתהליך הכתיבה גורם מוסמך מטעם מחלקת האימונים.</p>', '<p>אישור תיק יסוד:<br> על כל חיל להגדיר גורם מטה חילי, האחראי על ניהול עבודת המטה הזרועית לאישור תיקי יסוד, כל תיק יסוד היוצא לסבב אישורים מטעם ביה”ס, יאושר ראשית ע”י:<br> מפקד הגפייה, רע”ן או מג”ד ומפקד ביה”ס.</p>', '<p>עדכון תיק יסוד:<br> בטווח הזמן של חמש השנים בין אישורי תיקי היסוד, נדרש לתעד את כלל העדכונים החלים בהכשרה.<br> בבהל״ג נעשתה עבודה מאסיבית של תיקוף תיקי היסוד.<br>תיקי היסוד כעת נמצאים בתהליך אישור ע״י פיקוד ז״י.</p>'], 
        ['<p>מבחן הוא כלי המאפשר למפקדים להעריך את רמת הידע, ההבנה והכשירות של חניך במהלך ההכשרה.<br> התארגנות טרם מבחן: <br> טרם המבחן יש לפרסם לחניכים את  הנושאים עליהם הם עתידים להיבחן. במבחן מעשי נדרש לפרסם לחניכים את דף ההערכה טרם המבחן, יש לאפשר לחניכים זמן ללמידה עצמית טרם הבחינה.</p> ', '<p>פיתוח המבחן:<br> פיתוח המבחן ייעשה בהתאם לתורה המקצועית בנושא כתיבת מבחנים,<br> לכל מבחן עיוני ייבנה טופס תשובות (תשובון), <br>לכל מבחן מעשי ייבנה דף הערכה אשר יכלול את הקריטריונים להערכת הביצוע של הנבחן. ואת אופן חלוקת המשקלים הפנימית.</p>', '<p>בדיקה ומישוב המבחן:<br> בדיקה של המבחן תיעשה ביחס לדף התשובות המאושר, במבחן עיוני יש לפרסם את דף התשובות עם חלוקת הטפסים הבדוקים,<br> במבחן מעשי, יש לבצע משוב פרונטלי/כתוב לכל נבחן ע"ב דף ההערכה עד שבוע מיום הבחינה.<br> יש לקיים שיחה אישית עם חניך הנכשל במבחן עיוני/מעשי בה יוסברו לחניך נקודות הכשל והמשך תהליך הלמידה.</p>', '<p>*המבחנים העיוניים לא יעלו על 30% סך המבחנים המסמיכים בהכשרה.<br> *לכל חניך יינתנו שני מועדים לביצוע המבחן - מועד א’ ומועד ב.</p>'], 
        ['<p>תיק הכולל את התוכן המקצועי והפעולות ההדרכתיות, המרכיבות שיעור פרונטלי או פעילות בעלת מרכיבים מעשיים.<br> התיק נועד לאפשר הכנה מקצועית של המפקד או מדריך, להביא לאחידות מקצועית ולתרום לזיכרון הארגוני.</p>', '<p>כתיבה ותיקוף של תיק מדריך: <br> לכל יחידת לימוד יבוצעו תהליך פיתוח הדרכתי ע"פ התו"ל, לכל יחידת לימוד מסוג עיוני או מעשי ייכתב תיק מדריך, על כל יחידת לימוד יהיה חתום קצין בדרגת סגן לפחות</p>' ,''],
        ['<p>"פרויקט מפקדי חוליות" הוא פרויקט שמטרתו לאתר ולפתח חיילי הכשרה אשר ע"פ הקריטריונים הינם מתאימים להיות מפקדי חוליה כבר משלב הטירונות כחלק ממסגרת מבנה וארגון לקרב של חוליה לוחמת, תוך מתן דגש על פיקוד, הצבת סטנדרט גבוה וסיום ההכשרה בהצלחה.</p>', '<p>מתווה בחירת מפקד חוליה: <br> במהלך הגיוס - הפרויקט מאתר שלישותית חיילים אשר התגייסו בעלי מתאם קצונה 3-6. בנוסף, בשלב הראשוני של הגיוס בוחנים המפקדים את רמת המוטיבציה של החייל לשירות קרבי.</p>', '<p> חוו"ד מפקד:<br> חודש ראשון להכשרה: כש"ג - במהלך החודש הראשון להכשרה יבצעו כחלק מכלל המסגרת בוחן כש"ג ראשוני אשר יהווה גורם משפיע בהחלטת המפקדים. יכול להיות שהוא לא עבר אבל גם לא רחוק מלעבור בתום החודש הראשון להכשרה יעביר המפקד הישיר (מ"כ/מ"מ) של החייל חוו"ד מפקד למ"פ הפלוגה על כל חייל אשר בפוטנציאל להיות מפקד חוליה. בהתאם לחוו"ד מפקד גבוה בהשתכלל כלל הנתונים שצוינו לעיל יוחלט אם החייל נמצא מתאים לפרויקט מפקדי חוליות.</p>']],

        arrayThree: [['', ''],
        ['<p>איתור מפקדים להכשרות <br> 1.איכות ההכשרות והיכולות לעמוד במטרותיהן תלויות באופן ישיר בסגל פיקוד וההדרכה ובמקצועיותו. <br> 2.תהליכי האיתור מאפשרים ליחידות להבטיח כי המפקדים המתאימים ביותר ישובצו כמפקדים בהכשרות. <br></p>',
        '<p>3.כלל פוטנציאל המפקדים והמפקדות של חיל הגנת הגבולות מבצעים הכשרת פיקוד זוטר בקמ"אות("תבור" ו"רותם") ובקמ"כ "מצדה", לכן זהו השלב שבו יתבצע שלב האיתור לתפקידי הפיקוד<br> מטרת הנוהל:<br> 4.להגדיר את תהליכי האיתור של המפקדים בהכשרות.</p>', 
        '<p>הגדרות:<br> 5.מפקד בהכשרה- כל בעל תפקיד הלוקח חלק בביצוע או בניהול ההכשרה מתפקיד המ"פ ועד תפקיד המ"כ. בהגדרה זו נכללים גם בעלי תפקידים כגון משה"ד, רס"פ, סמל וכו. הוראה זו תתייחס לאנשי הסגל בדרג המ"כ והמ"מ(ספר התל"ב המטכ"לי 2013.).<br> 6.איתור- תהליך בחירת המועמדים המתאימים ביותר(העונים על תנאי הסף) מקרב אוכלוסיות שונות.</p>',
        '<p>עקרונות:<br> 7.לכל הכשרה יתקיים תהליך סדור של איתור של חניכים מומלצים ליציאה לפיקוד ואיתור של חניכים בקורסי המ"כים וההשלמות הקצונה כמפקדים בהכשרות.<br> 8.בא"ח-תהליך איתור החניכים המאותרים ליציאה לפיקוד-<br> א.גיבוש פוטנציאל בהתאם לקריטרניוים עד אמצע הטירונות.<br></p>',
        '<p>9.גדוד הפיקוד-שיבוץ מ"כים וקצינים בהכשרות:<br> ב.גיבוש המלצה לשיבוץ בהכשרות ע"ב קריטריונים מוגדרים עד שבועיים מסיום ההכשרה.<br> ג.שימוש בפורמט "כברת הדרך" גיליון הציונים והערכת המפקד והחניך עד הלום, שמשמש כתומך בקבלת ההחלטות.</p>', '<p> 10.התהליך על כל שלביו ינוהל ע"י מפקד בדרגת סא"ל(מג"ד/רע"ן).<br> 11.התהליך על כל שלביו יתועד במקום ייעודי אשר יוגדר ע"י הגורם המשא"ני בגדוד/ענף.</p>', 
        '<p>תמהיל אנשי סגל:<br> 12.בכל זמן נתון יש לשמר תמהיל של אנשי סגל וותיקים אל מול אנשי סגל חדשים ביחס המינימאלי המפורט:<br>א.הכשרות בא"ח-30% סגל וותיק אל מול חדש.<br>ב.הכשרות תצ"מ-30% סגל וותיק אל מול חדש.<br>ג.הכשרות פיקוד-50% סגל וותיק אל מול חדש.</p>', 
        '<p> 13.בכל זמן נתון יש לשמר תמהיל של אנשי סגל בעלי ניסיון תפקיד אליו מכשירים לעומת אנשי סגל ללא ניסיון(במקום בו אנשי הסגל משובצים ישירות מקורסי הפיקוד) ביחס של 30:70 בהתאמה.<br> 14.יש לשאוף להחלפת אנשי סגל תתקיים אך ורק בסיום מחזור ולא במהלכו.</p>',
        '<p>שלב איתור הפוטנציאל:<br> 15.בהכשרות הפיקוד (מ"כים והשלמות קצונה ומקביליהם) יש לסמן עד 30% חניכים "מאותרים להדרכה" בהכשרות בהל"ג. המאותרים להדרכה הם חניכים אשר בלטו לחיוב בהיבטים מנהיגותיים, מקצועיים והדרכתיים.<br> 16.בהכשרות בהם קיים דירוג שלישים לחניכים, יהיו המאותרים מהשליש העליון או האמצעי בדירוג.<br>17.איתור החניכים יתבצע על בסיס קריטריונים קבועים מראש אשר יוערכו במסגרת התרגילים וההתנסויות בהכשרה.</p>',
        '<p>שלב איתור הפוטנציאל:<br> 18.אוכלוסיית המאותרים תהווה לא פחות מ-80% מכלל הפוטנציאל לסגלי הפיקוד הנדרשת.<br> 19.להלן קריטריונים לסימון פוטנציאל מאותרים להדרכה-בא"ח ותצ"מ(מגמת סופה):<br>א.בעל ידע מקצועי רב.<br>ב.בעל ראייה מערכתית.<br>ג.יכולת העברת מסרים.<br>ד.יכולות חניכה גבוהות.<br>ה.יכולות למידה עצמית.<br>ו.יכולת עבודה בצוות וקבלת ביקורת.<br>ז.יכולות הובלה ורתימה.</p>',
        '20.להלן קריטריונים לסימון פוטנציאל מאותרים להדרכה במהלך ההכשרות בגדוד פיקוד:<br> יכולת העברת מסרים וחניכה, יחסי אנוש, מקצועיות, יכולת למידה עצמית, יכולת הובלה ורתימה, התנהלות ע"פ ערכי צה"ל.', '<p>1. איתור וסימון מפקדים במהלך הכשרתם בקק"ץ, שליחתם לתפקידי פיקוד ופיתוח וחזרתם לגדוד לאחר תפקיד באורך כ-8 חודשים.<br> 2.החניכים יחזרו לגדוד הפיקוד אך ורק אם הצטיינו בתפקיד המ"מ.<br>3.מ"כים יחזרו לגדוד לאחר ביצוע תפקיד באורך של כ-4 חודשים, כאשר בכל מחזור משאירים מ"כ מצטיין בתור מודרך בקורס מ"כים.<br> 4.תנאי לפיקוד בגדוד-איתור בקורס כמאותר פיקוד ושליש עליון.</p>',
        '<p>תיעוד וניהול הידע:<br> 21.אחריות התיעוד וניהול הידע היא של המפקדים בגדודים ויש לבצע בקרה של המג"דים בתחום זה.<br>22.החניכים המאותרים להדרכה יסומנו בתיק האישי במערכת המעל"ה במסגרת חוות הדעת המסכמת.</p>',
        '<p>מסמכים נוספרים קשורים<br>23.קובץ הוראות מקצועיות לתחום ההדרכה בבתי הספר הצבאיים ביבשה, דצמבר 2002, קובץ הוראות מס 3.<br> 24.תפיסת ההדרכה הצבאית,מטכ"ל 8- תת-02, טיוטה רשמית, התשע"ה-2014.<br>25.ההכשרות ביבשה, יבשה -8 זי-2, תמוז התשע"ג,ספטמבר 2013.</p>'],
        ['', 'הגדרות:  סגל הדרכה - כל בעל תפקיד המשתייך למסגרת המבצעת את ההדרכה בפועל. הכנת סגל - תהליך של בניית סגל כצוות עבודה. התוכנית להכנת סגל תיבנה ע”י, מפקד הפלוגה או מפקד הקורס בדרגת סגן לכל הפחות.', '', 'סיכום הכנת הסגל: לכל הכנת סגל יתבצע סיכום בראשות מפקד הפלוגה או מפקד ההכשרה.  כחלק מהסיכום יש לבצע תחקור והפקת לקחים בשיתוף כלל אנשי הסגל. במהלך ההכשרה יוקדש פרק לתחקור והפקות לקחים שבוצעה טרם המחזור ובמהלכה.'],
        ['הערכת איש הסגל היא תהליך שיטתי ומובן  המאורגן בתוכנית קבועה מראש. תהליך הערכת וחניכת איש סגל מתחילה מיום קליטתו כאיש סגל ועד לסיום תפקידו.',
         '', 
        '<p>שיטה:<br> 16.בניית תכנית הערכה לאיש סגל:<br> א.לכל איש סגל תיבנה תכנית חניכה והערכה אחת למחזור <br> ב.תכנית ההערכה והחניכה תיבנה ע"ב אבחון של מפקדי המוערך/נחנך.<br> ג.תכנית ההערכה והחניכה תוצג לאיש הסגל בשבוע הראשון של הכנת הסגל. <br> ד.תכנית ההערכה והחניכה תכלול מבחנים מסמיכים ומופעי חניכה.</p>',
        '<p>17.מימוש ובקרה<br> א.ניהול המערכת החונכת יבוצע ע"י המ"פ. הוא הנושא באחריות לכך שלכל איש סגל בפלוגה תיכתב תכנית הערכה וחניכה.<br> ב.בקרה-תבוצע ע"י המ"פ וקצינות הצפ"ה בגדודים.</p>',
        '<p>18.מופעי וכלי הערכה וחניכה<br> א.מבחן רמה מקצועי-מבחן מקצועי יתבצע אחת לארבעה חודשים או אחת למחזור(הקצר מבין השניים). מבחן הרמה יהיה מעשי ועיוני ויכלול את הידע המקצועי אשר נדרש ממפקד בהכשרה. מבחן הרמה מהווה את שמירת הכשירות המקצועית. מבחן זה יתקיים במהלך הכנת הסגל ויהווה תנאי לפתיחת המחזור.</p>',
        '<p>ב.בוחן כושר גופני-אחת לחודשיים או אחת למחזור(הקצר מבין שניים) יתקיים בוחן כש"ג אשר יכלול את ההישג הנדרש לאיש הסגל בתחום הכושר הגופני. בוחן זה יתקיים במהלך הכנת הסגל ויהווה תנאי לפתיחת מחזור.</p>',
        '<p>ג.בוחן הדרכה-אחת לחודשיים או אחת למחזור(הקצר מבין השניים) יוערך המפקד על יכולותיו בביצוע תהליכי חניכה והערכת חיילים, ועל יכולותיו להעביר שיעור. מבחן זה יתקיים במהלך הכנת הסגל ויהיה תנאי לפתיחת מחזור.</p>',
        '<p>ד.משוב מפקד(מופע חניכה)- אחת לחודשיים או פעמיים במחזור הכשרה יבצע המפקד הממונה על איש הסגל, תצפית סדורה במופע פורמאלי שמוביל איש הסגל המוערך כגון- הובלת תרגיל, הובלת מסדר, ביצוע תדריך, העברת שיעור וכיו"ב. לפחות מופע חניכה אחד במחזור יהיה "חניכה על חניכה", כלומר, מישוב על האופן בו המפקד חונך את חייליו.</p>',
        '<p> ה.חוו"ד תקופתי של מפקד ישיר- במהלך כל תקופת תפקידו יחנך המפקד על-ידי מפקדו הישיר באופן פורמלי(כמתואר בסעיף הקדום) ובאופן בלתי פורמאלי על תפקידו כמפקד-"חניכה לתפקיד". אחד לחודשיים או אחד למחזור(הקצר מבין השניים) ימלא ויעביר מפקדו הישיר של איש הסגל חוות דעת. חוות הדעת. חוות הדעת תכלול מדדים קבועים ואובייקטיבים בנושאים הבאים: מקצועיות, פיקוד, הדרכה וערכים. חוות הדעת תהיה מבוססת על כלל הכלים שפורטו בסעיף זה וכן כלים נוספים כגון חוו"ד עמיתים ומשוב חניכים.</p>',
        'עקרונות העבודה: כל איש סגל לרבות סגל הדרכה, יוערך באופן שיטתי וקבוע במהלך  תפקידו, עם תכנית החניכה של ההכשרה. תכנית החניכה מוצגת בשבוע הראשון להכנת הסגלים.חלק מכלי ההערכה יירשמו במהלך הכנת הסגל כתנאי לפתיתת מחזור  מופעי החניכה, יבוצעו עייפ עקרונות החניכה.',
        ''],
        [''],
        ['הידע והניסיון שצוברים בעלי התפקידים במהלך הקדנציות הוא חיוני ומשמעותי לצורך קיום הכשרה אפקטיבית. מטרת הנוהל: להגדיר את מנגנוני כניסת וסיום התפקיד של מפקדים בכירים בדרג מ”פ ומעלה בהכשרות. הגדרות: מפקדים בכירים בהכשרות -  לרוב מפקדים מדרג המ”פ עד דרג המג”ד. כניסה לתפקיד - פרק הזמן המוגדר בו רוכש המפקד הנכנס ידע ומיומנויות הנדרשות למילוי תפקידו.', 'תדירות תחלופת מפקדים ומשך קדנציות: מפקדים בדרג מ"מ ומקביליו יבצעו תפקיד של 8 חודשים לכל הפחות שני מחזורי הכשרה. מפקדים בדרג ממ"ג יבצעו תפקיד של שמונה חודשים עד שנה, בהתאם לציר הפיתוח שנקבע ע"י החיל. רמ"ד מקצועות בענף תצ"מ יבצע תפקיד של שנתיים לכל הפחות. מפקדים בדרג המג"ד יבצעו תפקיד של     שנתיים לכל הפחות.', 'מסמך סיום התפקיד יכלול את הסעיפים הבאים:  עיקרי התמורות אשר חלו בהכשרה בתקופת תפקידו.  לקחים ותובנות. סוגיות מומלצות לקידום לטובת שיפור ההכשרה.', 'היערכות לכניסה לתפקיד: כל מפקד בכיר נכנס יקבל תיק נתונים שלושה שבועות טרם כניסתו לתפקיד. אחריות העברת התיק היא של המפקד היוצא אלא אם הוגדר אחרתע”י מפקד הבה”ד.'],
        ['התהליך יפרט את אופן סיום התפקיד של איש הסגל שאינו מתאים לתפקידו, בשל פערים משמעותיים בביצוע התפקיד או בשל אירוע חריג אחר.  סיבות להרחקת מפקד  בהכשרה: אירוע חריג- אירוע שאינו עומד בנורמות הערכיות המצופות בצה"ל. הרחקה מסיבות פיקודית כתוצאה מחוסר התאמה. הרחקה בעקבות פער מקצועי. הרחקה מסיבה משמעתית כתוצאה מעבירה צבאית, או מהתנהגות שאינה הולמת.  הרחקה מסיבה רפואית. הרחקה במקרבה של מקרה בפגיעה בנפש או אובדנות.', ' מרגע ההחלטה יש לוודא כי נלקח מאיש הסגל נשקו האישי עד שיוחלט אחרת. ניתן לאפשר לאיש הסגל להגיש ערעור שיעובר למפקד בית הספר עד 48 שעות מרגע קבלת ההחלטה. תהליך הרחקת איש סגל מתבצע לאחר תהליך הערכה מסודר, הבוחן רקע מקצועי ופיקודי.'],
        ['עולם ההכשרות והלמידה הוא עולם משתנה המושפע מגורמים רבים, ביניהם ההתפתחות הטכנולוגית, שינויים בתו”ל וכו’. פורום הדרכה בה”די נועד לקדם תהליכי הדרכה המושפעים מהשינויים לעיל ותכליתם היא שיפור וטיוב ההכשרות בבהייד. בכל בה”ד יתקיימו שני סוגים של פורומים, פורום בכיר- יתקיים כל תקה”ד. ופורום מורחב- יתקיים אחת לחציון.', 'נושאים בהם יעסקו הפורומים: הפקת לקחים רוחביים מתקופת הדרכה - אחת לחציון, הטמעת טכנולוגיות ותפיסות בהכשרות, פערים ומגמות בנורמות ובתהליכי ההדרכה כתוצאה ממופעי בקרה פנימיים, שיטות למידה חדשניות, הצגת תהליך שבוצע באחת ההכשרות בעל פוטנציאל ליישום רוחבי, עדכוני תו”ל ואמל”ח בעלי משמעויות רוחביות להכשרות, הצגת מוקדי תחקיר בנושאים הרלוונטיים לאיכות ההכשרות, הצגת תחקירים מבצעיים בעלי השפעה על ההכשרות, כל נושא אחר בעל השלכות על כלל הפעילות ההדרכתית בבה”ד.'],
        ['שיחות מפקד בהל״ג הינן בגדר תוכן חובה הן בהכנות הסגלים והן בהכשרות לקצונה. האחרויות לתיכנון השיחות הן של מפקד המסגרת המכשירה או של הגורם המתאם. עיתוי השיחות: העמקה חיילית לקמ״פ - שיחת ׳צידה לדרך׳ למ״פ תתואם ליום האחרון או ליום לפני אחרון ההשתלמות.  שבוע המ״פ בהכשרה - שיחת ׳תפיסת תפקיד המ״פ׳ תתואם במהלך השבוע הראשון.', ' השתלמות מ״פים נכנסים - שיחת ׳המ״פ בהכשרה׳, תתואם כשיחה הראשונה של ההשתלמות. שיחות מפקד להכנות הסגלים, תתואם בדר״כ לשבוע האחרון בהכנות הסגלים בקורסי הפיקוד והשלמות הקצונה ובתחילת הכנת הסגל של הבא״ח. שיחות מפקד להשלמות הקצונה - שיחת ׳צידה לדרך׳ למ״מ, תתואם לשבועיים האחרונים של ההכשרות, טרם מופע סיכום הצוערים.'],
        ['מסלולי קידום מוגדרים לאנשי סגל ההדרכה מהווים חשיבות רבה בשתי רמות: ברמת בית הספר: מסלולי קידום מוגדרים וברורים תורמים למיצוי הפוטנציאל של סגלי ההדרכה. ברמת סגל ההדרכה:  מסלולי קידום מוגדרים וברורים יוצרים לסגלי ההדרכה אופק מקצועי ברור, וכן הגברה בניעה להדרכה והמשך צבאי.', 'התנאים של מדריכים בתחומים ייעודיים להתקדמות במסלולי הקידום, יוגדרו על פי המרכיבים הבאים: מוטיבציית החייל לתפקיד. חוו”ד מפקדים. עמידה בכשירות.', 'קידומי סגלי ההדרכה בבהל”ג: ביצוע תפקיד מ”כ, ביצוע תפקידי סמל/ רס”פ/ משה”ד/ פיתוח לקצונה, סמ”פ, יציאה לקמ”פ, מ”פ, מסלולים נוספים בהחלטת החיל.']],


        arrayFour: [['', 'חייל יכול להתקשות בתחומים הבאים: שפה, כושר, קליעה, תוכן מקצועי או בכל תחום אחר שבו ההכשרה נוגעת. דרכי הפעולה לקידום החניך:'],
        ['מופע המתקיים כאשר יש ספק לגבי ההתאמה של החניך לתפקידו. בתום הוודעה יחליטו אם החניך יודח מהתפקיד או יושאר בהכשרה. בשל הרגישות הרבה, נדרש להקפיד על תהליך סדור, הוגן והדרגתי מול החניך.', 'עילות להעלאת חניך לוועדת הערכה: הפסד ימי הכשרה ע”פ המוגדר בתיק יסוד, אירוע ערכי/ נורמטיבי/ משמעתי, אי עמידה בציוני הסף.', 'יש לשקף לחניך את תוצאות הוועדה בצורה ברורה ולוודא כי הוא הבין את המעשה ומדוע ההחלטה התקבלה.'],
        ['תהליך בחירת חניך מצטיין וחניך למופת מעודדת מצוינות ומאפשרת להדגיש את ההתנהגות וההישגים הרצויים בהכשרה. תהליך בחירת חניך מצטיין וחניך למופת נועד להגביר את הרצון להשקיע בהכשרה ולתגמל חניכים שהשקיעו בהכשרה ושהגיעו להישגים שבולטים לטובה.', 'התבחינים להצטיינות למופת יתייחסו למרכיבים האלה: חיילות והתנהגות ערכית,  הישגים לימודיים,  התנהגות ראויה לציון לשבח: למשל סיוע לחברי הקורס, יוזמה, התנדבות, ללא עבירות משמעת.'],
        ['תיק אישי לחניך משמש אמצעי לאיסוף המידע וריכוזו במכלול נושאים בעלי רלוונטיות לחניכים בקורס. הוא מהווה מעקב מסודר ושיטתי ומאפשר איתור מוקדם של בעיות לימודיות, חברתיות ואישיות.', ''],
        ['']],
        
        arrayFive: [['ההדרכה על אמל”ח ביבשה עוברת בשנים האחרונות תהליך שינוי במסגרתו הלמידה מתבצעת יותר על אמצעים מדמים ופחות על האמל”ח בפועל. השינוי נובע הן משיקולי  התייעלות והן מההתפחות הטכנולוגית המאפשרת דימוי איכותי ויכולות תחקור מתקדמות.', 'תהליך הקליטה של המאמן יובל ע”י רע”ן תצ”מ ורמ”ד המקצועות בשיתוף מטה ביה”ס. יש להוציא כתב מינוי בחתימת מפקד הבה”ד, לקליטת המאמן והפונקציות המוגדרות בתהליך. תהליך הקליטה יבוצע לאחר למידה ובדיקה מעמיקה של המאמן בתעשייה המפתחת.'],
        ['בצה”ל קיים נוהל המגדיר תהליך ייזום של התוויה וקליטה של אמל”ח חדש. הוראת ה’קליטה האג”מית נכתבת ע”י החיל ומופצת כפקודה לבית הספר כחצי שנה טרם תחילת ההכשרה בהכשרות. יש להגדיר את הכשירות והמדדים של הפעלת האמל”ח לכל אחד מבעלי התפקידים הנחשפים אליו.', 'על וועדת הקליטה לנהל גאנט הכולל את הנושאים הבאים: מעקב אחר הגעת האמצעים להדרכה, מעקב אחר פיתוח תכולות ההדרכה, מעקב אחר הכנת הסגלים, מעקב אחר עדכון תיקי היסוד הרלוונטים.'],
        ['תשתיות ההדרכה מהוות נדבך מרכזי ביכולת להוציא לפועל את תכניות ההכשרה השונות ביחידות זרוע היבשה. ע”מ לשמר את תשתיות ההדרכה, יש לקיים מנגנון סדור לאחזקתן. תשתיות הדרכה- כל מבנה שמטרתו לקיים באמצעותו למידה או אימון לרבות כיתות מסוגים שונים, טווחים, לש”ביות ומאמנים.', 'בע”ת האחראים על אחזקת הכיתות. האחריות הכוללת על אחזקת תשתיות ההדרכה, היא אחריות אג”מ בהל”ג. סגן מפקד היחידה ומטה.  ביה”ס יכינו רשימה של כלל תשתיות ההדרכה בבה”ד והקצינים הממונים על כל תשתית. לדוגמא: קשר”ח- על מחשבים ומקרנים, ק. בינוי על חלונות וסורגים, וכו’.']],
        choosen: this.subNumber,
        i: this.innerPart,
        j: 0,
        graphicsArray: [[
    ]]

    }
  }, 
  methods: {
    startPage() {
        this.$emit("curr-page", 2);
    },
    myButtons() {
        if(event.target.id === "back_button"){
            if(this.j > 0){
                 document.getElementById("next_chapter").style.display="none";
                document.getElementById("next_button").style.display="block";
                this.j = this.j - 1;
            }else if(this.j <= 0){
                document.getElementById("back_button").style.display="none";
                document.getElementById("last_chapter").style.display="block";
                document.getElementById("next_chapter").style.display="none";
                document.getElementById("next_button").style.display="block";
            }
        }else if(event.target.id === "next_button"){
            document.getElementById("last_chapter").style.display="none";
            if(this.choosen === 0){
                if(this.arrayOne[this.i][this.j+1] === undefined){
                    document.getElementById("next_button").style.display="none";
                     document.getElementById("next_chapter").style.display="block";
                   this.j = this.j ;
                }else {
                    
                   this.j = this.j + 1;
                }
            }else if(this.choosen === 1){
                if(this.arrayTwo[this.i][this.j+1] === undefined){
                    document.getElementById("next_button").style.display="none";
                     document.getElementById("next_chapter").style.display="block";
                   this.j = this.j ;
                }else {
                     document.getElementById("next_button").style.display="block";
                   this.j = this.j + 1;
                }
            }else if(this.choosen === 2){
                if(this.arrayThree[this.i][this.j+1] === undefined){
                    document.getElementById("next_button").style.display="none";
                     document.getElementById("next_chapter").style.display="block";
                   this.j = this.j ;
                }else {
                     document.getElementById("next_button").style.display="block";
                   this.j = this.j + 1;
                }

            }else if(this.choosen === 3){
                if(this.arrayFour[this.i][this.j+1] === undefined){
                    document.getElementById("next_button").style.display="none";
                     document.getElementById("next_chapter").style.display="block";
                   this.j = this.j ;
                }else {
                     document.getElementById("next_button").style.display="block";
                   this.j = this.j + 1;
                }
            }else {
                if(this.arrayFive[this.i][this.j+1] === undefined){
                    document.getElementById("next_button").style.display="none";
                     document.getElementById("next_chapter").style.display="block";
                    this.j = this.j ;
                }else {
                     document.getElementById("next_button").style.display="block";
                    this.j = this.j + 1;
                }
            }
        }
    },
    nextChapter(){
        this.$emit('make-progress', this.i);
        this.i = this.i + 1;
        this.j = 0;
        document.getElementById("next_button").style.display="block";
        document.getElementById("next_chapter").style.display="none";
    },
    lastChapter(){
        this.$emit('made-progress', this.i);
        this.i = this.i - 1;
        this.j = 0;
        document.getElementById("last_chapter").style.display="none";
         document.getElementById("back_button").style.display="block";
    }
  },
}
</script>


<style>
