    font-weight: 700vw;
    font-size: 4.3vw;
    right: 2%;
    width: 95%;
    height: 75%;
    line-height: 2;
    margin-top: 17%;
}


#back_button {
    position: absolute;
    background-image: url("../assets/symbols/BackwardsButton.svg");
