    width: 15%;
    height: 10%;
    bottom: 0%;
    right: 2%;

}

#next_button {
    position: absolute;
    background-image: url("../assets/symbols/NextButton.svg");
    background-size: contain;
    background-repeat: no-repeat;
    width: 15%;
    height: 10%;
     bottom: 0%;
     left: 2%;
}

#next_chapter{
     position: absolute;
   width: 30%;
    height: 10%;
     bottom: 0%;
